import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash, faTimes } from '@fortawesome/free-solid-svg-icons';
import FlipMove from 'react-flip-move';

export const ListItem = (props) => {
    const { items, deleteItem } = props;
     return (
       <>
         <FlipMove duration={800}>
           {items.length > 0 ? (
             items.map(item => (
               <div
                 className="text-white bg-pink-700 h-16 mx-6 my-auto mt-2 rounded flex justify-between"
                 key={item.key}
               >
                 <p className="p-4">{item.text}</p>
                 <span onClick={() => deleteItem(item.key)}>
                   <FontAwesomeIcon
                     className="text-white mt-6 mr-4 cursor-pointer shadow hover:text-gray-400"
                     icon={faTrash}
                   />
                 </span>
               </div>
             ))
           ) : (
             <div className="text-gray-500 text-xl bg-pink-700 h-16 mx-6 my-auto mt-2 rounded flex justify-center">
               <p className="p-4">No Text</p>
               <p className="p-4 text-center">
                 <FontAwesomeIcon icon={faTimes} />
               </p>
             </div>
           )}
         </FlipMove>
       </>
     );
}