import React, {useState }from 'react'

export const FromTodo = (props) => {

  const { addItem } = props;
  
  const [CurrentUser, setCurrentUser] = useState({ text: '', key: '' })

  const handleInput = (event) => {
    const { name, value } = event.target;    setCurrentUser({...CurrentUser,[name]:value})
  };

  const addSubmitItem = (e) => {
    e.preventDefault();
    if (!CurrentUser.text) return
    addItem(CurrentUser);
    setCurrentUser({ text: '', key: '' });
  }
     return (
         <form onSubmit={addSubmitItem}>
        <div>
          < input type="text" placeholder="Enter text" name="text" value={CurrentUser.text} onChange={handleInput}
            className = "shadow leading-normal bg-teal-600 py-2 w-64 h-12 px-2 m-6 rounded text-xl focus:outline-none placeholder-white text-white"/>
          <button  type="submit" className="h-12 w-20 rounded bg-yellow-600 font-bold text-white focus:outline-none hover:bg-yellow-700">Add</button>          
        </div>
      </form>
     )
}