import React, { useState}from 'react';
import './styles/app.css'
import { FromTodo } from './components/FromTodo';
import { ListItem } from './components/ListItem';
export const App = () => {

  const todoList = [
    { text: "Mi Princesa", key: Date.now()}
  ]
  const [items, setItems] = useState(todoList);

  const addItem = (item) => {
    item.key = Date.now();
    setItems([...items, item]);
  }

  const deleteItem = (key) => {
    const filterItem = items.filter(item => item.key !== key);
    setItems(filterItem);
  }

  return (
    <div className="container mx-auto w-full max-w-md bg-purple-700 shadow-md rounded px-8 pt-6 pb-8 mb-2 mt-16" >
      <FromTodo addItem={addItem}/>  
    <div>
        <ListItem items={items} deleteItem={deleteItem}/>
      </div>
    </div>
  );
}
